/* index.css */

/* Navbar Styles */
.navbar {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(79, 247, 33);
    position: sticky;
    top: 0;
}

.navbar ul {
    display: flex;
    list-style: none;
    margin: 0;
}

.navbar ul li {
    font-family: century;
    font-size: 1.1rem;
    font-weight: bold;
    margin-right: 20px;
    Margin-top: 20px;
}

.navbar ul li a {
    text-decoration: none;
    color: white;
    padding: 10px;
    transition: all .5s ease;
}

.navbar ul li a:hover {
    background-color: white;
    color: black;
    box-shadow: 0 0 10px white;
}

/* Home Section Styles */
.home {
    text-align: center;
    padding: 50px;
}

.home h1 {
    font-family: 'Arial';
    font-size: 70px;
}

.home h2 {
    font-family: 'cursive';
    font-size: 40px;
    color: blue;
}

.home button {
    padding: 10px 35px;
    background-color: orange;
    border: 1px solid white;
    color: black;
    outline: none;
    transition: .6s ease;
    border-radius: 20px;
    cursor: pointer;
}

.home button:hover {
    background-color: white;
    color: black;
    box-shadow: 0 0 5px white, 0 0 15px white;
    font-weight: bold;
}

/* Media Queries for Responsive Design */
@media only screen and (max-width: 768px) {
    .navbar {
        flex-direction: column;
        align-items: flex-start;
        padding: 10px;
    }

    .navbar ul {
        flex-direction: column;
        align-items: center;
    }

    .navbar ul li {
        margin: 10px 0;
    }

    .home {
        padding: 20px;
    }

    .home h1 {
        font-size: 50px;
    }

    .home h2 {
        font-size: 30px;
    }
}